<template>
  <div id="design-picture">
    <div class="left">
      <div class="title" style="text-align: center">
        <h2></h2>
      </div>
      <div class="left-content">
        <h3></h3>
        <!--  -->
        <!-- default-active="0-0-0" -->
        <!-- menuCountryList -->
        <el-menu
          :default-active="'0-0-0'"
          class="el-menu-vertical-demo"
          :unique-opened="true"
          @select="selectMenu"
        >
          <el-submenu
            :index="firstIdx + ''"
            v-for="(item, firstIdx) in menuPlatFormList"
            :key="firstIdx"
          >
            <template slot="title">{{ item }}</template>
            <el-submenu
              v-for="(pro, secIdx) in menuCountryList[firstIdx]"
              :key="secIdx"
              :index="firstIdx + '-' + secIdx"
            >
              <template slot="title">{{ pro }}</template>
              <el-menu-item
                v-for="(sku, proIdx) in menuSiteList[firstIdx][secIdx]"
                :key="proIdx"
                :index="firstIdx + '-' + secIdx + '-' + proIdx"
                >{{ sku }}</el-menu-item
              >
            </el-submenu>
          </el-submenu>
        </el-menu>

        <!-- <el-menu
          default-active="0-0-0"
          ref="designMenu"
          class="el-menu-vertical-demo"
          @open="openMenu"
        >
          <el-submenu
            :index="index + ''"
            v-for="(item, index) in menuPlatFormList"
            :key="index"
          >
            <template slot="title">
              <span style="display:inline-block;width:100%">{{ item }}</span>
            </template>
            <el-submenu
              :index="index + '-' + index2"
              v-for="(item2, index2) in showCountryList"
              :key="index2"
            >
              <template slot="title">
                <span
                  style="display:inline-block;width:100%"
                  @click="secondClick(index2)"
                  >{{ item2 }}</span
                >
              </template>
              <el-menu-item
                :index="index + '-' + index2 + '-' + index3"
                v-for="(item3, index3) in lastSiteList"
                :key="index3"
                @click="thirdClick(item, item2, item3)"
              >
                <span>{{ item3 }}</span>
              </el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu> -->
      </div>
    </div>
    <div class="right">
      <div class="button" @click="sortClick">
        <el-button size="mini"></el-button>
        <el-button size="mini"></el-button>
        <el-button size="mini"></el-button>
        <el-button size="mini">A+</el-button>
        <el-button size="mini"></el-button>
      </div>
      <div class="select-all">
        <el-checkbox
          :indeterminate="isIndeterminate"
          v-model="checkAll"
          @change="handleCheckAllChange"
          ></el-checkbox
        >
        <!-- <el-button type="primary" size="mini" @click="batchDownload"></el-button> -->
        <el-button type="primary" size="mini" @click="BatchDownload"
          ></el-button
        >
      </div>
      <div v-if="!isInstructions">
        <div class="pic-list">
          <div class="pic" v-for="(item, index) in picList" :key="index">
            <div class="pic-show">
              <img :src="_urlzgl + item[0]" alt />
            </div>
            <div class="pic-title">
              <el-checkbox-group
                v-model="picCheckedList"
                @change="handleCheckedListChange"
              >
                <el-checkbox :label="index">
                  {{ item[0].slice(item[0].lastIndexOf("/") + 1) }}
                </el-checkbox>
              </el-checkbox-group>
            </div>
            <div class="download">
              <!-- <a :href="item" download target="_blank"></a> -->
              <el-button
                @click="lookBigPic(item[0])"
                class="pointer"
                size="mini"
                ></el-button
              >
              <!-- <el-button type="primary" size="mini" @click="imgDownload(item)"></el-button> -->
              <!-- <a
                :href="'http://www.beyoung.group/design/download_single_pic/?product_code='+checkedSite+'&item='+checkedSort+'&file_name='+item[0].slice(item[0].lastIndexOf('/')+1)+'&encoding='+item[1]"
              ></a>-->
              <!-- <a :href="getImageUrl(item[0],checkedSite,checkedSort,item[1])" target="_blank"></a> -->
              <el-button @click="imgDownload(item[0], item[1])" size="mini"
                ></el-button
              >
              <el-button size="mini" @click="viewLinks(item[0], item[1])"
                ></el-button
              >
            </div>
          </div>
        </div>
      </div>
      <div v-else class="all-instructions">
        <div v-for="(item, index) in picList" :key="index" class="instruction">
          <!-- <iframe :src="item" width="400px" height="400px"></iframe> -->
          <div class="instruction-link">
            <el-checkbox-group
              v-model="picCheckedList"
              @change="handleCheckedListChange"
            >
              <el-checkbox :label="index">{{ "" }}</el-checkbox>
            </el-checkbox-group>
            <a :href="item" target="_blank"
              >{{ item.slice(item.lastIndexOf("/") + 1) }}</a
            >
          </div>
        </div>
      </div>
    </div>

    <el-dialog
      title=""
      :visible.sync="viewLinksVisible"
      width="30%"
      center
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="viewClose"
    >
      <span>{{ linkPath }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="viewLinksVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
