     */
    getProductData() {
      this.$api.getProductData().then(res => {
        console.log(res);
        this.menuPlatFormList = res.data.type_list;
        this.menuCountryList = res.data.spu_list;
        this.menuSiteList = res.data.product_name_list;
        // 
        this.showCountryList = this.menuCountryList[0];
        this.showSiteList = this.menuSiteList[0];
        this.lastSiteList = this.showSiteList[0];
        this.checkedPlatform = this.menuPlatFormList[0];
        this.checkedCountry = this.showCountryList[0];
        this.checkedSite = this.lastSiteList[0];

        this.checkedSort = "";
        this.$api.picQueryInfo(this.checkedSite, this.checkedSort).then(res => {
          console.log(res);
          this.picList = res.data.data;
        });
      });
    },
    /**
