     * @return {*}
     */
    sortClick(ev) {
      this.picCheckedList = [];
      this.checkAll = false;
      this.isIndeterminate = false;
      // console.log(ev);
      console.log(ev.target.innerText);
      // 
      this.picList = [];
      switch (ev.target.innerText) {
        case "":
          this.checkedSort = "";
          this.isInstructions = false;
          break;
        case "":
          this.checkedSort = "";
          this.isInstructions = false;
          break;
        case "":
          this.checkedSort = "";
          this.isInstructions = false;
          break;
        case "A+":
          this.checkedSort = "A";
          this.isInstructions = false;
          break;
        case "":
          this.checkedSort = "";
          this.isInstructions = true;
          break;
      }
      if (this.checkedSort !== "") {
        this.$api.picQueryInfo(this.checkedSite, this.checkedSort).then(res => {
          console.log(res);
          this.picList = res.data.data;
        });
      } else {
        this.$api
          .instructionsInfo(this.checkedSite, this.checkedSort)
          .then(res => {
            console.log(res);
            this.picList = res.data.data;
          });
      }
    },
    /**
     * @description: 
     * @param {*} val
     * @return {*}
     */
    handleCheckAllChange(val) {
      // returncheckboxlabel
      this.picCheckedList = val
        ? this.picList.map((item, index) => {
            return index;
          })
        : [];
      this.isIndeterminate = false;
    },
    // 
    handleCheckedListChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.picList.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.picList.length;
    },
    // 
    // imgDownload(url) {
    //   console.log(url);
    //   const image = new Image();
