    //       link.href = url;
    //       link.setAttribute("download", name);
    //       document.body.appendChild(link);
    //       link.click();
    //     });
    // },
    imgDownload(url, encoding) {
      let link = document.createElement("a");
      link.style.display = "none";
      url = this.getImageUrl(url, this.checkedSite, this.checkedSort, encoding);
      link.href = url;
      // link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    /**
