    //       link.href = url;
    //       link.setAttribute("download", name);
    //       document.body.appendChild(link);
    //       link.click();
    //     });
    //   }
    // },
    //url blob
    getImgArrayBuffer(url) {
      let _this = this;
      return new Promise((resolve, reject) => {
        //blob
        let xmlhttp = new XMLHttpRequest();
        xmlhttp.open("GET", url, true);
        xmlhttp.responseType = "blob";
        xmlhttp.onload = function() {
          if (this.status == 200) {
            resolve(this.response);
          } else {
            reject(this.status);
          }
        };
        xmlhttp.send();
      });
    },
    // imgDataUrl url
    BatchDownload() {
      if (this.picCheckedList.length == 0) {
        this.$message.closeAll();
        return this.$message.error("");
      }

      // console.log("");

      // let _this = this;
      // let zip = new JSZip();
      // let cache = {};
      // let promises = [];
      // _this.title = "";

      // let imgUrl = [];
      // for (let i in this.picCheckedList) {
      //   const item = this.picCheckedList[i];
      //   const list = this.picList;
      //   let name = list[item][0].slice(list[item][0].lastIndexOf("/") + 1);
      //   let obj = {};
      //   // if (this.checkedSort !== "A" && this.checkedSort !== "") {
      //   obj["path"] =
      //     "https://www.beyoung.group/design/download_single_pic/?product_code=" +
      //     this.checkedSite +
      //     "&item=" +
      //     this.checkedSort +
      //     "&file_name=" +
      //     name +
      //     "&encoding=" +
      //     list[item][1];
      //   console.log("aasdsads");
      //   // }
      //   obj["name"] = name;
      //   console.log(obj);
      //   imgUrl.push(obj);
      // }

      // for (let item of imgUrl) {
      //   const promise = _this.getImgArrayBuffer(item.path).then(data => {
      //     // , ArrayBuffer(blob)
      //     zip.file(item.name, data, { binary: true }); // 
      //     cache[item.name] = data;
      //   });
      //   promises.push(promise);
      // }

      // Promise.all(promises)
      //   .then(() => {
      //     zip.generateAsync({ type: "blob" }).then(content => {
      //       _this.title = "";
      //       // 
      //       FileSaver.saveAs(content, ""); // file-saver  
      //       _this.title = "";
      //     });
      //   })
      //   .catch(res => {
      //     _this.$message.error("");
      //   });
      let imgUrl = [];
      if (this.checkedSort !== "A" && this.checkedSort !== "") {
        for (let i in this.picCheckedList) {
          const item = this.picCheckedList[i];
          const list = this.picList;
          let encoding = list[item][1];
          let name = list[item][0].slice(list[item][0].lastIndexOf("/") + 1);
          imgUrl.push(
            encoding +
              "/" +
              this.checkedSite +
              "/main_figure/" +
              this.checkedSort +
              "/" +
              name
          );
        }
        // this.$api.imgDownLoad(imgUrl).then(res => {
        //   console.log(res);
        //   let url = window.URL.createObjectURL(new Blob([res.data]));
        //   let link = document.createElement("a");
        //   link.style.display = "none";
