      }
      // 
      else if (this.checkedSort === "") {
        for (let i in this.picCheckedList) {
          const item = this.picCheckedList[i];
          const list = this.picList;
          // 
          let u = list[item].replace(
            "http://www.beyoung.group/static/images/design_data_se/",
            ""
          );
          imgUrl.push(u);
        }
        this.$api.getProductData().then(res => {
          let url = imgUrl.join("@");
          url = "https://www.beyoung.group/download_single_pic/?path=" + url;
          // url = "http://106.53.250.215:8005/download_single_pic/?path=" + url;
          console.log(url);

          let link = document.createElement("a");
          link.style.display = "none";
          link.href = url;
          link.setAttribute("download", ".zip");
          document.body.appendChild(link);
          link.click();
        });
      }
      // A
      else {
        for (let i in this.picCheckedList) {
          const item = this.picCheckedList[i];
          const list = this.picList;
          let encoding = list[item][1];
          let name = list[item][0].slice(list[item][0].lastIndexOf("/") + 1);
          imgUrl.push(encoding + "/a_add_img/" + name);
        }
        this.$api.getProductData().then(res => {
          let url = imgUrl.join("@");
          url = "https://www.beyoung.group/download_single_pic/?path=" + url;
          // url = "http://106.53.250.215:8005/download_single_pic/?path=" + url;
          console.log(url);

          let link = document.createElement("a");
          link.style.display = "none";
          link.href = url;
          link.setAttribute("download", ".zip");
          document.body.appendChild(link);
          link.click();
        });
      }
    },
    // 
    selectMenu(index) {
      console.log(index);
      let arr = index.split("-");
      let firstIdx = arr[0];
      let secIdx = arr[1];
      let thirdIdx = arr[2];

      this.picList = [];
      console.log("");
      this.checkedPlatform = this.menuPlatFormList[firstIdx];
      this.checkedCountry = this.menuCountryList[secIdx];
      this.checkedSite = this.menuSiteList[firstIdx][secIdx][thirdIdx];
      this.isInstructions = false;
      this.checkedSort = "";
      this.picCheckedList = [];
      this.checkAll = false;
      this.isIndeterminate = false;
      this.$api.picQueryInfo(this.checkedSite, this.checkedSort).then(res => {
        console.log(res);
        this.picList = res.data.data;
      });
    },

