      console.log(index);
      this.lastSiteList = [];
      this.lastSiteList = this.showSiteList[index];
    },
    // 
    thirdClick(platform, country, site) {
      this.picList = [];
      console.log("");
      this.checkedPlatform = platform;
      this.checkedCountry = country;
      this.checkedSite = site;
      this.isInstructions = false;
      this.checkedSort = "";
      this.picCheckedList = [];
      this.checkAll = false;
      this.isIndeterminate = false;
      this.$api.picQueryInfo(site, this.checkedSort).then(res => {
        console.log(res);
        this.picList = res.data.data;
      });
    },
    /**
     * @description: url
     * @param {name,checkedSite,checkedSort}
     * @return {*}
     */

    lookBigPic(url) {
      // 
      let name = url.slice(url.lastIndexOf("/") + 1);
      // 
      this.$api
        .getBigPic(this.checkedSite, this.checkedSort, name)
        .then(res => {
          console.log(res);
          window.open(res.data.data, "_blank");
        });
    },
    // 
    getImageUrl(url, site, sort, encoding) {
      if (url.indexOf("+") >= 0) {
        url = url.replace("+", "replaceStr");
      }
      return (
        "https://www.beyoung.group/design/download_single_pic/?product_code=" +
        site +
        "&item=" +
        sort +
        "&file_name=" +
        url.slice(url.lastIndexOf("/") + 1) +
        "&encoding=" +
        encoding
      );
    },
    /**
     * @description: 
     * @param {*} encoding
     * @return {*}
     */
    viewLinks(url, encoding) {
      let type = "";
      console.log(type);
      // url 
      url = url.slice(url.lastIndexOf("/") + 1);
      url = url.slice(0, url.lastIndexOf("."));
      console.log(url);
      // A
      if (this.checkedSort !== "A") {
        type = "";
        this.$api
          .viewLinks(encoding, this.checkedSite, type, url, this.checkedSort)
          .then(res => {
            console.log(res);
            if (res.data.data.length > 0 && res.data.data[0].picture_path) {
              this.linkPath = res.data.data[0].picture_path;
              this.viewLinksVisible = true;
            }
          });
      } else {
        type = "A";
        this.$api
          .viewLinks(encoding, this.checkedSite, type, url, "")
          .then(res => {
            console.log(res);
            this.linkPath = res.data.data[0].picture_path;
            this.viewLinksVisible = true;
          });
      }
      // this.viewLinksVisible = true;
    },
    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    viewClose() {
      this.linkPath = "";
    }
  },
  created() {
    this.getProductData();
  },
  mounted() {}
};
</script>
<style scoped>
#design-picture {
  width: 100vw;
  height: 92vh;
  position: relative;
}
.title {
  margin-top: 2vh;
}
.left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  border-right: 1px solid black;
  top: 0;
  left: 0;
  overflow: auto;
}
.right {
  position: absolute;
  top: 0;
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  left: 18vw;
  padding: 24px;
  overflow: auto;
}
.left-content h3 {
  margin-top: 2vh;
  text-align: center;
}
.pic {
  width: 15vw;
  height: 26vh;
  /* border: 1px solid black; */
  margin-bottom: 14px;
  text-align: center;
}
.pic .pic-show {
  width: 70%;
  height: 60%;
  /* border: 1px solid black; */
  margin: 0 auto;
  margin-top: 2vh;
}
.pic .pic-show img {
  width: 100%;
  height: 100%;
}
.pic-list {
  margin-top: 16px;
  display: flex;
  justify-content: left;
  align-items: center;
  flex-wrap: wrap;
}
.pic .pic-title {
  width: 100%;
  height: 4vh;
  margin-top: 6px;
  /* border: 1px solid black; */
  text-align: center;
}
.select-all {
  margin-top: 6px;
}
.pointer {
  cursor: pointer;
}
.all-instructions {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 12px;
  /* margin-left: 4vw; */
}
.instruction {
  /* flex: 1; */
  /* width: 600px;
  height: 600px; */
  display: flex;
  flex-direction: column;
  border: 1px solid #333;
  padding: 6px;
  margin-bottom: 16px;
  border-radius: 12px;
}
.instruction iframe {
  border-radius: 12px;
}
#design-picture >>> .el-dialog--center .el-dialog__body {
  text-align: center;
}
.el-checkbox-group {
  display: inline-block;
}
.instruction-link {
  text-align: center;
}
</style>



